 import {LitElement, html, css} from 'lit';
 import {customElement, property} from 'lit/decorators.js';
 
 @customElement('mi-evento-captura')
 export class MiEventoCaptura extends LitElement {
   static override styles = css`
     :host {
       display: block;
       border: solid 1px gray;
       padding: 16px;
       max-width: 800px;
     }
   `;
  @property() contador = '';

   override render() {
     return html`
      <p @count-changed=${this._countListener}></p>
      <p>Login: ${this.contador}</p>
     `;
   }
private _countListener(e: CustomEvent) {
 this.contador = e.detail.contador;
 }
 }
 
 declare global {
   interface HTMLElementTagNameMap {
     'mi-evento-captura': MiEventoCaptura;
   }
 }
 